.checkmate-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.checkmate-content {
  background-color: #000033;
  border: 2px solid #00ff00;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  color: #00ff00;
  box-shadow: 0 0 20px #00ff00;
}

.checkmate-content h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-shadow: 0 0 10px #00ff00;
}

.checkmate-content p {
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.checkmate-content button {
  background-color: #000033;
  border: 2px solid #00ff00;
  color: #00ff00;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.2rem;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.checkmate-content button:hover {
  background-color: #00ff00;
  color: #000033;
  box-shadow: 0 0 10px #00ff00;
}