.notebook {
  background-color: rgba(0, 26, 51, 0.7);
  border: 2px solid #00ff00;
  border-radius: 10px;
  padding: 20px;
  height: 100%;
  overflow-y: auto;
  max-height: calc(100vh - 200px);
  width: 100%;
  box-sizing: border-box;
}

.notebook h2 {
  color: #00ff00;
  text-align: center;
  margin-bottom: 20px;
}

.notebook ol {
  list-style-type: none;
  padding: 0;
}

.notebook li {
  margin-bottom: 10px;
  font-family: 'Courier New', monospace;
  color: #00ffff;
}

.move-number {
  display: inline-block;
  width: 30px;
  color: #00ffff;
}

.white-move, .black-move {
  display: inline-block;
  margin-right: 10px;
}