body {
  background-color: #000000;
  color: #00ff00;
  font-family: 'Orbitron', sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 10px 20px;
  box-sizing: border-box;
}

h1 {
  font-size: 2.5rem;
  text-shadow: 0 0 10px #00ff00;
  margin-bottom: 5px;
  text-align: center;
}

.subtitle {
  margin-top: 0;
  margin-bottom: 10px;
}

.game-layout {
  display: flex;
  flex: 1;
  overflow: hidden;
  gap: 20px;
}

.chessboard-container {
  flex: 0 0 65%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 10px;
}

.notebook-container {
  flex: 0 0 30%;
  overflow: hidden;
}

button {
  background-color: #000033;
  color: #00ff00;
  border: 2px solid #00ff00;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
  align-self: center;
}

button:hover {
  background-color: #00ff00;
  color: #000033;
  box-shadow: 0 0 10px #00ff00;
}